import React from "react"
import Layout from "../components/layout"
import WorldMap from "../components/map/worldMap"
import SEO from "../components/seo"
import "../styles/map.css"
import SubNav from "../components/subnav"
import {useTranslation, Trans} from 'react-i18next';
import { Link } from 'gatsby'

const IndexPage = ({ pageContext: { stats } }) => {
  const { t, i18n } = useTranslation();

  const bibles = stats.bible_count.toLocaleString(i18n.language)
  const resources = stats.resource_count.toLocaleString(i18n.language)
  const organizations = stats.organization_count.toLocaleString(i18n.language)

  return(
      <Layout>
        <SEO title={ t('title') } lang={ i18n.language } />
        <SubNav />
        <WorldMap />

        { (i18n.language.substring(0, 2) == 'en') ?
        <section className="medium-8 columns centered text-center mt-20">
          Find-A-Bible provides a directory with links to:<br />
          <Link to='/bibles'>{bibles} Bible Versions</Link> and {resources} Biblical Resources from <Link to='/organizations'>{organizations} Organizations</Link>.
        </section> : ''}

        { (i18n.language.substring(0, 2) == 'en') ?
          <div>
            <section className="row text-center mt-20">
              <div className="medium-3 columns">
                <img src="https://images.bible.cloud/fab/fab_color_language.svg" /><br />
                { t('index.question_looking_for_bible') }
              </div>
              <div className="medium-3 columns">
                <img src="https://images.bible.cloud/fab/fab_color_traveler.svg" /><br />
                { t('index.question_traveling')}
              </div>
              <div className="medium-3 columns">
                <img src="https://images.bible.cloud/fab/fab_color_group.svg" /><br />
                { t('index.question_missionary') }
              </div>
              <div className="medium-3 columns">
                <img src="https://images.bible.cloud/fab/fab_color_present.svg" /><br />
                { t('index.question_outreach') }
              </div>
            </section>
          <div style={{backgroundColor:"#000",color:"#FFF"}}>
            <div className="row mt-20 pd-20">
            <div className="columns medium-10 centered">
              <p className="text-center">The Find-a-Bible project exists to serve people worldwide by:</p>
              <div className="columns small-hide medium-3 large-2">
                <img src="https://images.bible.cloud/icon_target_white.svg" />
              </div>
              <div className="columns small-12 medium-9 large-10">
                <ul className="square">
                  <li>Informing users about Bible resources in available languages</li>
                  <li>Providing missions agencies with Bible translation information and status</li>
                  <li>Equipping Christian workers with immediate access to available resources</li>
                  <li>Promoting FOBAI member resources and organizations</li>
                </ul>
              </div>
            </div>
            </div>
          </div>
          <div className="row mt-20">
            <div className="medium-8 columns centered">
          <img src="https://images.bible.cloud/fab_color_globe.svg" />
          <p>Find-A-Bible seeks to provide comprehensive data on existing Bible resources in the 6000+ language of the world.</p>
          <p>Would you help us ensure that Bible resources can be discovered, secured, and shared? If you know of Bible resources not found in this directory, let us know!</p>
          <p className="text-centered">
          <Link to="/about/contact"> Contact Us for more information! </Link>
          </p>
          </div>
          </div>
          </div>

          : '' }




      </Layout>
  )
}

export default IndexPage