import React, { Component } from "react"
import {
  ComposableMap,
  ZoomableGroup,
  Geographies,
  Geography
} from "react-simple-maps"
import { scaleLinear } from "d3-scale"
import { navigate } from "gatsby"
import { Motion, spring } from "react-motion"
import MapContinentZoom from "./MapContinentZoom"

const wrapperStyles = {
  width: "100%",
  height: "500px",
  margin: "0 auto"
}

const popScale = scaleLinear()
.domain([0,100000000,1400000000])
.range([
    "#FFF",
    "#9b9",
    "#696"
])

const watchListScale = scaleLinear()
  .domain([0,11,40,50])
  .range([
      "#E62",
      "#F93",
      "#FC2",
      "#FFF"
])

class WorldMap extends Component {
  constructor(props) {
    super(props)

    this.state = {
      popFocus: true,
      center: [0,20],
      zoom: 1
    }

    this.handleZoomIn = this.handleZoomIn.bind(this)
    this.handleZoomOut = this.handleZoomOut.bind(this)
    this.handleRegionClick = this.handleRegionClick.bind(this)

    this.switchToPopulation = this.switchToPopulation.bind(this)
    this.switchToWatchList = this.switchToWatchList.bind(this)
    this.handleClick = this.handleClick.bind(this)
    this.handleMove = this.handleMove.bind(this)
    this.handleLeave = this.handleLeave.bind(this)
  }

  handleZoomIn() {
    this.setState({
      zoom: this.state.zoom * 2,
    })
  }

  handleZoomOut() {

    if(this.state.zoom < 2.5) {
      this.setState({
        zoom: 1
      })
    } else {
      this.setState({
        zoom: this.state.zoom / 2,
      })
    }

  }

  handleRegionClick(data) {
    this.setState({
      center: data.coordinates,
      zoom: data.zoom
    })
  }

  switchToPopulation() {
    this.setState({ popFocus: true })
  }

  switchToWatchList() {
    this.setState({ popFocus: false })
  }

  handleClick(geography, evt) {
    navigate("/countries/" + geography.properties.iso_a2);
  }

  handleMove(geography, evt) {
    //HeaderWithTooltip.setTooltipContent(geography.properties.name);
    //withTooltip(evt.target, {
    //  title: geography.properties.name,
    //});

  //  this.setState() = evt.clientX
  //  this.state.toolTipY + window.pageYOffset
  //  this.state.toolTipContent = geography.properties.name
  }
  handleLeave() {
  //  this.state.toolTipX = "-10000px"
  //  this.state.toolTipY + window.pageYOffset
  //  this.state.toolTipContent = geography.properties.name
  }



  render() {


    return (
      <div className="map-background">
        <div>
          <div className="map-zoom-in" onClick={ this.handleZoomIn }>+</div>
          <div className="map-zoom-out" onClick={ this.handleZoomOut }>-</div>
          <div className="map-pop-data" onClick={ this.switchToPopulation }>W</div>
          <div className="map-reg-data" onClick={ this.switchToWatchList }>P</div>
          <MapContinentZoom handleRegionClick={this.handleRegionClick} />
        </div>
        <div style={wrapperStyles}>
          <Motion
            defaultStyle={{
              zoom: 1,
              x: 0,
              y: 0,
            }}
            style={{
              zoom: spring(this.state.zoom, {stiffness: 210, damping: 20}),
              x: spring(this.state.center[0], {stiffness: 210, damping: 20}),
              y: spring(this.state.center[1], {stiffness: 210, damping: 20}),
            }}
          >
            {({zoom,x,y}) => (
          <ComposableMap
            projectionConfig={{
              scale: 205,
              rotation: [0,0,0],
            }}
            width={980}
            height={550}
            style={{
              width: "100%",
              height: "500px",
            }}
          >
            <ZoomableGroup center={[x,y]} zoom={zoom}>
              <Geographies
                geography={ "/world-50m-with-population.json" }
                disableOptimization
              >
                {(geographies, projection) =>
                  geographies.map((geography, i) => (
                    <Geography
                      key={`${geography.properties.iso_a3}-${i}`}
                      cacheId={`${geography.properties.iso_a3}-${i}`}
                      geography={ geography }
                      projection={ projection }
                      onClick={this.handleClick}
                      onMouseMove={this.handleMove}
                      onMouseLeave={this.handleLeave}
                      round
                      style={{
                        default: {
                          fill: (this.state.popFocus)
                            ? watchListScale(geography.properties.world_watch_list)
                            : popScale(geography.properties.pop_est),
                          stroke: "#444",
                          strokeWidth: 0.75,
                          outline: "none",
                        },
                        hover: {
                          fill: this.state.popFocus
                            ? "#F93"
                            : "#6c9d6c",
                          stroke: "#222",
                          strokeWidth: 0.75,
                          outline: "none",
                        },
                        pressed: {
                          fill: this.state.popFocus
                            ? "#6c9d6c"
                            : popScale(geography.properties.pop_est),
                          stroke: "#607D8B",
                          strokeWidth: 0.75,
                          outline: "none",
                        }
                      }}
                    />
                  ))}
              </Geographies>
            </ZoomableGroup>
          </ComposableMap>
            )}
          </Motion>
        </div>
      </div>
    )
  }
}

export default WorldMap