import React, { Component } from 'react'
import Europe from "../../../assets/europe.svg"
import Africa from "../../../assets/africa.svg"
import Asia from "../../../assets/asia.svg"
import Australia from "../../../assets/australia.svg"
import NorthAmerica from "../../../assets/northamerica.svg"
import SouthAmerica from "../../../assets/southamerica.svg"

class MapContinentZoom extends Component {

  render() {
    return (
      <div className="continent-nav row">
        <div className="small-2 columns" onClick={
            ((e) => this.props.handleRegionClick({coordinates: [130, -20], zoom: 2.3 }))}>
          <Australia />
        </div>

        <div className="small-2 columns" onClick={
            ((e) => this.props.handleRegionClick({coordinates: [13, 0], zoom: 2.2 }))}>
          <Africa />
        </div>

        <div className="small-2 columns" onClick={
            ((e) => this.props.handleRegionClick({coordinates: [-65, -25], zoom: 2.4 }))}>
          <SouthAmerica />
        </div>

        <div className="small-2 columns" onClick={
            ((e) => this.props.handleRegionClick({coordinates: [-95, 40], zoom: 2 }))}>
          <NorthAmerica />
        </div>

        <div className="small-2 columns" onClick={
            ((e) => this.props.handleRegionClick({coordinates: [100, 30], zoom: 2 }))}>
          <Asia />
        </div>

        <div className="small-2 columns" onClick={
            ((e) => this.props.handleRegionClick({coordinates: [10, 50], zoom: 3 }))}>
          <Europe />
        </div>
      </div>
    )
  }
}

export default MapContinentZoom